import React, { useState, useMemo } from 'react';
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
  ReferenceLine
} from 'recharts';
import { ACWRData, getRiskColor } from '../lib/acwr';
import { Calendar, Filter } from 'lucide-react';
import ACWRProgressIndicator from './ACWRProgressIndicator';

interface ACWRChartProps {
  data: ACWRData[];
  daysWithData?: number;
  isDarkMode?: boolean;
  teamAverageData?: ACWRData[];
  showTeamAverage?: boolean;
}

export function ACWRChart({
  data,
  daysWithData = 0,
  isDarkMode = false,
  teamAverageData = [],
  showTeamAverage = false
}: ACWRChartProps) {
  const [selectedPeriod, setSelectedPeriod] = useState<'week' | 'month' | 'quarter' | 'all'>('month');
  const [isTeamAverageVisible, setIsTeamAverageVisible] = useState(showTeamAverage);

  const MINIMUM_DAYS = 21;
  const isDataSufficient = daysWithData >= MINIMUM_DAYS;

  // 
  const filteredData = useMemo(() => {
    if (data.length === 0) return [];

    const today = new Date();
    let startDate: Date;

    switch (selectedPeriod) {
      case 'week':
        startDate = new Date(today);
        startDate.setDate(today.getDate() - 7);
        break;
      case 'month':
        startDate = new Date(today);
        startDate.setDate(today.getDate() - 30);
        break;
      case 'quarter':
        startDate = new Date(today);
        startDate.setDate(today.getDate() - 90);
        break;
      case 'all':
      default:
        return data;
    }

    return data.filter((item) => {
      const itemDate = new Date(item.date);
      return itemDate >= startDate && itemDate <= today;
    });
  }, [data, selectedPeriod]);

  // 
  const filteredTeamData = useMemo(() => {
    if (teamAverageData.length === 0) return [];

    const today = new Date();
    let startDate: Date;

    switch (selectedPeriod) {
      case 'week':
        startDate = new Date(today);
        startDate.setDate(today.getDate() - 7);
        break;
      case 'month':
        startDate = new Date(today);
        startDate.setDate(today.getDate() - 30);
        break;
      case 'quarter':
        startDate = new Date(today);
        startDate.setDate(today.getDate() - 90);
        break;
      case 'all':
      default:
        return teamAverageData;
    }

    return teamAverageData.filter((item) => {
      const itemDate = new Date(item.date);
      return itemDate >= startDate && itemDate <= today;
    });
  }, [teamAverageData, selectedPeriod]);

  if (data.length === 0) {
    return (
      <div className="space-y-4">
        {daysWithData > 0 && (
          <ACWRProgressIndicator daysWithData={daysWithData} isDarkMode={isDarkMode} />
        )}
        <div className="flex items-center justify-center h-64 sm:h-96 text-gray-500 dark:text-gray-400">
          <div className="text-center px-4">
            <p className="text-base sm:text-lg mb-2"></p>
            <p className="text-sm">ACWR</p>
          </div>
        </div>
      </div>
    );
  }

  if (!isDataSufficient) {
    return (
      <div className="space-y-4">
        <ACWRProgressIndicator daysWithData={daysWithData} isDarkMode={isDarkMode} />
        <div className="flex items-center justify-center h-64 sm:h-96 text-gray-500 dark:text-gray-400 border-2 border-dashed border-gray-300 dark:border-gray-600 rounded-lg">
          <div className="text-center px-4">
            <p className="text-base sm:text-lg mb-2">ACWR</p>
            <p className="text-sm">{MINIMUM_DAYS - daysWithData}ACWR</p>
          </div>
        </div>
      </div>
    );
  }

  const formatDate = (dateStr: string) => {
    const d = new Date(dateStr);
    return `${d.getMonth() + 1}/${d.getDate()}`;
  };

  const formatDateWithDay = (dateStr: string) => {
    const d = new Date(dateStr);
    const dayNames = ['', '', '', '', '', '', ''];
    return `${d.getMonth() + 1}/${d.getDate()}(${dayNames[d.getDay()]})`;
  };

  //  Tooltip: payload[0] name 
  const CustomTooltip = ({ active, payload, label }: any) => {
    if (!active || !payload || payload.length === 0) return null;

    // Rechartspayload
    const personalEntry =
      payload.find((p: any) => p?.name === ' ACWR') ||
      payload.find((p: any) => p?.dataKey === 'acwr'); // 

    const teamEntry =
      payload.find((p: any) => p?.name === '');

    const personal = personalEntry?.payload; // filteredData1
    const personalACWR =
      typeof personalEntry?.value === 'number' && Number.isFinite(personalEntry.value)
        ? personalEntry.value
        : (typeof personal?.acwr === 'number' ? personal.acwr : null);

    // labelX(date)
    const date = new Date(label);
    const isWeekend = date.getDay() === 0 || date.getDay() === 6;

    //  filteredTeamData 
    const teamRow = isTeamAverageVisible
      ? filteredTeamData.find((td) => td.date === label)
      : null;

    const teamACWR =
      teamRow && typeof teamRow.acwr === 'number' && Number.isFinite(teamRow.acwr)
        ? teamRow.acwr
        : (typeof teamEntry?.value === 'number' && Number.isFinite(teamEntry.value) ? teamEntry.value : null);

    if (!personal) return null;

    const diff =
      typeof personalACWR === 'number' && typeof teamACWR === 'number'
        ? personalACWR - teamACWR
        : null;

    return (
      <div className="bg-white p-3 sm:p-4 border border-gray-200 rounded-lg shadow-lg max-w-xs">
        <div className="flex items-center space-x-2 mb-2">
          <p className="font-medium text-gray-900 text-sm sm:text-base">{formatDateWithDay(label)}</p>
          {isWeekend && (
            <span className="bg-blue-100 text-blue-700 px-2 py-1 rounded-full text-xs font-medium">
              
            </span>
          )}
        </div>

        <div className="space-y-1 text-xs sm:text-sm">
          <p className="flex justify-between">
            <span> ACWR:</span>
            <span className="font-semibold" style={{ color: getRiskColor(personal.riskLevel) }}>
              {typeof personalACWR === 'number' ? personalACWR.toFixed(2) : '-'}
            </span>
          </p>

          {isTeamAverageVisible && teamACWR != null && (
            <p className="flex justify-between">
              <span>:</span>
              <span className="font-semibold text-purple-600">
                {teamACWR.toFixed(2)}
              </span>
            </p>
          )}

          {isTeamAverageVisible && diff != null && (
            <p className="flex justify-between text-xs">
              <span>:</span>
              <span className={`font-semibold ${diff > 0.3 ? 'text-red-600' : 'text-gray-600'}`}>
                {diff > 0 ? '+' : ''}{diff.toFixed(2)}
              </span>
            </p>
          )}

          <p className="flex justify-between">
            <span>:</span>
            <span className="font-semibold">{personal.acuteLoad ?? '-'}</span>
          </p>
          <p className="flex justify-between">
            <span>:</span>
            <span className="font-semibold">{personal.chronicLoad ?? '-'}</span>
          </p>
          <p className="flex justify-between">
            <span>:</span>
            <span className="font-semibold" style={{ color: getRiskColor(personal.riskLevel) }}>
              {getRiskLabel(personal.riskLevel)}
            </span>
          </p>
        </div>
      </div>
    );
  };

  const CustomDot = (props: any) => {
    const { cx, cy, payload } = props;
    if (cx && cy && payload) {
      const d = new Date(payload.date);
      const isWeekend = d.getDay() === 0 || d.getDay() === 6;

      return (
        <circle
          cx={cx}
          cy={cy}
          r={isWeekend ? 5 : 4}
          fill={getRiskColor(payload.riskLevel)}
          strokeWidth={2}
          stroke={isWeekend ? '#3B82F6' : 'white'}
          opacity={isWeekend ? 0.9 : 1}
        />
      );
    }
    return null;
  };

  return (
    <div className="w-full space-y-4">
      {daysWithData < 28 && (
        <ACWRProgressIndicator daysWithData={daysWithData} isDarkMode={isDarkMode} />
      )}

      {/*  */}
      <div className="mb-4 space-y-3">
        <div className="flex items-center space-x-2">
          <Calendar className="w-5 h-5 text-gray-500" />
          <span className="text-sm font-medium text-gray-700"></span>
        </div>

        <div className="flex items-center space-x-2">
          <Filter className="w-4 h-4 text-gray-500" />
          <div className="flex space-x-1">
            {[
              { value: 'week', label: '1' },
              { value: 'month', label: '1' },
              { value: 'quarter', label: '3' },
              { value: 'all', label: '' }
            ].map((period) => (
              <button
                key={period.value}
                onClick={() => setSelectedPeriod(period.value as any)}
                className={`px-3 py-1 rounded-lg text-xs font-medium transition-colors ${
                  selectedPeriod === period.value
                    ? 'bg-blue-600 text-white'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                {period.label}
              </button>
            ))}
          </div>
        </div>

        {/*  */}
        <div className="bg-blue-50 rounded-lg p-3">
          <div className="flex items-center justify-between text-sm text-blue-700">
            <div>
              <span className="font-medium">:</span> {filteredData.length}
              {selectedPeriod !== 'all' && (
                <span className="ml-2">
                  ({selectedPeriod === 'week'
                    ? '7'
                    : selectedPeriod === 'month'
                      ? '30'
                      : '90'})
                </span>
              )}
            </div>
            <div className="flex items-center space-x-1">
              <div className="w-3 h-3 bg-blue-500 rounded-full border-2 border-blue-600"></div>
              <span className="text-xs"></span>
            </div>
          </div>
        </div>

        {/*  */}
        {teamAverageData.length > 0 && (
          <div className="flex items-center space-x-2">
            <button
              onClick={() => setIsTeamAverageVisible(!isTeamAverageVisible)}
              className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors ${
                isTeamAverageVisible
                  ? 'bg-purple-600 text-white hover:bg-purple-700'
                  : 'bg-gray-100 text-gray-700 hover:bg-gray-200 dark:bg-gray-700 dark:text-gray-300 dark:hover:bg-gray-600'
              }`}
            >
              {isTeamAverageVisible ? '' : ''}
            </button>
            {isTeamAverageVisible && (
              <span className="text-xs text-gray-600 dark:text-gray-400">
                
              </span>
            )}
          </div>
        )}
      </div>

      <div className="h-64 sm:h-96">
        <ResponsiveContainer width="100%" height="100%">
          <LineChart data={filteredData} margin={{ top: 20, right: 30, left: 20, bottom: 20 }}>
            <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />

            <XAxis
              dataKey="date"
              tickFormatter={formatDate}
              stroke="#6b7280"
              fontSize={10}
              interval="preserveStartEnd"
            />
            <YAxis domain={[0, 'dataMax + 0.5']} stroke="#6b7280" fontSize={10} width={40} />
            <Tooltip content={<CustomTooltip />} />

            {/* risk zones */}
            <ReferenceLine y={1.5} stroke="#EF4444" strokeDasharray="5 5" />
            <ReferenceLine y={1.3} stroke="#F59E0B" strokeDasharray="5 5" />
            <ReferenceLine y={0.8} stroke="#10B981" strokeDasharray="5 5" />

            <Line
              type="monotone"
              dataKey="acwr"
              stroke="#6366f1"
              strokeWidth={2}
              dot={<CustomDot />}
              activeDot={{ r: 6, stroke: '#6366f1', strokeWidth: 2, fill: 'white' }}
              name=" ACWR"
            />

            {isTeamAverageVisible && filteredTeamData.length > 0 && (
              <Line
                type="monotone"
                data={filteredTeamData}
                dataKey="acwr"
                stroke="#9333ea"
                strokeWidth={2}
                strokeDasharray="5 5"
                dot={{ r: 3, fill: '#9333ea', stroke: '#9333ea' }}
                activeDot={{ r: 5, stroke: '#9333ea', strokeWidth: 2, fill: 'white' }}
                name=""
              />
            )}
          </LineChart>
        </ResponsiveContainer>
      </div>

      {/* Legend */}
      <div className="mt-4 flex justify-center">
        <div className="grid grid-cols-2 sm:flex sm:flex-wrap gap-2 sm:gap-4 text-xs">
          <div className="flex items-center">
            <div className="w-3 h-3 bg-red-500 rounded-full mr-2"></div>
            <span> (&gt;1.5)</span>
          </div>
          <div className="flex items-center">
            <div className="w-3 h-3 bg-yellow-500 rounded-full mr-2"></div>
            <span> (1.3-1.5)</span>
          </div>
          <div className="flex items-center">
            <div className="w-3 h-3 bg-green-500 rounded-full mr-2"></div>
            <span> (0.8-1.3)</span>
          </div>
          <div className="flex items-center">
            <div className="w-3 h-3 bg-blue-500 rounded-full mr-2"></div>
            <span> (&lt;0.8)</span>
          </div>
        </div>
      </div>
    </div>
  );
}

function getRiskLabel(riskLevel: string): string {
  switch (riskLevel) {
    case 'high':
      return '';
    case 'caution':
      return '';
    case 'good':
      return '';
    case 'low':
      return '';
    default:
      return '';
  }
}