// src/hooks/useTodayNutritionTotals.js
import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import { supabase } from "../lib/supabase";

/**
 *  nutrition_logs (kcal/P/F/C)
 * UX:
 * - loading totals  0 
 * - 
 * - refetch 
 */
export function useTodayNutritionTotals(userId, dateStr) {
  const [logs, setLogs] = useState([]);
  const [status, setStatus] = useState("idle"); // idle | loading | ready | error
  const [error, setError] = useState(null);
  const [reloadKey, setReloadKey] = useState(0);

  //  totals loading0
  const lastTotalsRef = useRef({ cal: 0, p: 0, f: 0, c: 0 });

  const refetch = useCallback(() => {
    setReloadKey((k) => k + 1);
  }, []);

  useEffect(() => {
    if (!userId || !dateStr) return;

    let cancelled = false;

    async function fetchLogs() {
      setStatus("loading");
      setError(null);

      try {
        const { data, error } = await supabase
          .from("nutrition_logs")
          .select(
            "id, record_date, meal_type, meal_slot, total_calories, p, f, c, is_edited, advice_markdown, image_url, image_path, analysis_status, analysis_error, created_at"
          )
          .eq("user_id", userId)
          .eq("record_date", dateStr)
          .order("meal_type", { ascending: true })
          .order("meal_slot", { ascending: true })
          .order("created_at", { ascending: true });

        if (error) throw error;

        if (!cancelled) {
          setLogs(data ?? []);
          setStatus("ready");
        }
      } catch (e) {
        console.error("[useTodayNutritionTotals] fetch error:", e);
        if (!cancelled) {
          setLogs([]);
          setStatus("error");
          setError(String(e?.message ?? e));
        }
      }
    }

    fetchLogs();
    return () => {
      cancelled = true;
    };
  }, [userId, dateStr, reloadKey]);

  const totals = useMemo(() => {
    if (status === "loading") {
      // loadingUX
      return lastTotalsRef.current;
    }

    const sum = { cal: 0, p: 0, f: 0, c: 0 };
    let hasUnparsed = false;

    for (const row of logs) {
      if (
        row.total_calories == null ||
        row.p == null ||
        row.f == null ||
        row.c == null
      ) {
        hasUnparsed = true;
      }

      sum.cal += Number(row.total_calories ?? 0);
      sum.p += Number(row.p ?? 0);
      sum.f += Number(row.f ?? 0);
      sum.c += Number(row.c ?? 0);
    }

    sum.p = Math.round(sum.p * 10) / 10;
    sum.f = Math.round(sum.f * 10) / 10;
    sum.c = Math.round(sum.c * 10) / 10;

    const result = {
      ...sum,
      hasAnyLog: logs.length > 0,
      hasUnparsed,
    };

    lastTotalsRef.current = result;
    return result;
  }, [logs, status]);

  return {
    logs,
    totals,
    loading: status === "loading",
    status, // UI
    error,
    refetch,
  };
}