import React, { useState, useEffect, useCallback } from 'react';
import { User, Team, supabase } from '../lib/supabase';
import { PaginatedTable } from './PaginatedTable';
import { UserEditModal } from './UserEditModal';
import { UserDeleteModal } from './UserDeleteModal';
import { Users, Edit, Trash2, AlertCircle, Search, Filter } from 'lucide-react';

interface UserManagementProps {
  teams: Team[];
  restrictToOrganizationId?: string;
}

export function UserManagement({ teams, restrictToOrganizationId }: UserManagementProps) {
  const [users, setUsers] = useState<User[]>([]);
  const [filteredUsers, setFilteredUsers] = useState<User[]>([]);
  const [loading, setLoading] = useState(true);
  const [selectedUser, setSelectedUser] = useState<User | null>(null);
  const [showEditModal, setShowEditModal] = useState(false);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [roleFilter, setRoleFilter] = useState<string>('all');

  const fetchUsers = useCallback(async () => {
    setLoading(true);
    setError(null);
    try {
      if (restrictToOrganizationId) {
        // Fetch users belonging to the restricted organization
        const { data: orgMembers, error: orgError } = await supabase
          .from('organization_members')
          .select('user_id')
          .eq('organization_id', restrictToOrganizationId);

        if (orgError) throw orgError;

        const userIds = orgMembers?.map(m => m.user_id) || [];

        if (userIds.length === 0) {
          setUsers([]);
          return;
        }

        const { data, error } = await supabase
          .from('users')
          .select('*')
          .in('id', userIds)
          .neq('role', 'global_admin')
          .order('created_at', { ascending: false });

        if (error) throw error;
        setUsers(data || []);
      } else {
        // Fetch all users (admin view)
        const { data, error } = await supabase
          .from('users')
          .select('*')
          .order('created_at', { ascending: false });

        if (error) throw error;
        setUsers(data || []);
      }
    } catch (err: any) {
      console.error('Error fetching users:', err);
      setError(': ' + err.message);
    } finally {
      setLoading(false);
    }
  }, [restrictToOrganizationId]);

  useEffect(() => {
    fetchUsers();
  }, [fetchUsers]);

  // Filter users based on search term and role filter
  useEffect(() => {
    let filtered = users;

    // Apply search filter
    if (searchTerm) {
      filtered = filtered.filter(user =>
        user.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        user.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
        (user.user_id && user.user_id.toLowerCase().includes(searchTerm.toLowerCase()))
      );
    }

    // Apply role filter
    if (roleFilter !== 'all') {
      filtered = filtered.filter(user => user.role === roleFilter);
    }

    setFilteredUsers(filtered);
  }, [users, searchTerm, roleFilter]);

  const handleUserUpdated = () => {
    setShowEditModal(false);
    setSelectedUser(null);
    fetchUsers();
  };

  const handleUserDeleted = () => {
    setShowDeleteModal(false);
    setSelectedUser(null);
    fetchUsers();
  };

  const handleEditClick = (user: User) => {
    setSelectedUser(user);
    setShowEditModal(true);
  };

  const handleDeleteClick = (user: User) => {
    setSelectedUser(user);
    setShowDeleteModal(true);
  };

  const getTeamName = (teamId: string | null) => {
    if (!teamId) return '-';
    const team = teams.find(t => t.id === teamId);
    return team ? team.name : '';
  };

  const getRoleLabel = (role: string) => {
    switch (role) {
      case 'athlete': return '';
      case 'staff': return '';
      case 'global_admin': return '';
      default: return role;
    }
  };

  const getRoleBadgeStyle = (role: string) => {
    switch (role) {
      case 'global_admin': return 'bg-purple-100 text-purple-800';
      case 'staff': return 'bg-green-100 text-green-800';
      case 'athlete': return 'bg-blue-100 text-blue-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const renderUserRow = (user: User, index: number) => (
    <div key={user.id} className="bg-white border border-gray-200 rounded-lg p-4 hover:bg-gray-50 transition-colors">
      {/* Mobile Layout */}
      <div className="block sm:hidden space-y-3">
        <div className="flex items-center justify-between">
          <div className="flex-1 min-w-0">
            <div className="flex items-center space-x-2 mb-1">
              <span className="font-mono text-xs bg-gray-100 text-gray-700 px-2 py-1 rounded">
                {user.user_id || 'N/A'}
              </span>
              <span className={`px-2 py-1 rounded-full text-xs font-medium ${getRoleBadgeStyle(user.role)}`}>
                {getRoleLabel(user.role)}
              </span>
            </div>
            <h3 className="font-semibold text-gray-900 truncate">{user.name}</h3>
            <p className="text-sm text-gray-600 truncate">{user.email}</p>
            <p className="text-xs text-gray-500">{getTeamName(user.team_id)}</p>
          </div>
          <div className="flex space-x-2 ml-2">
            <button
              onClick={() => handleEditClick(user)}
              className="p-2 rounded-full bg-blue-50 hover:bg-blue-100 text-blue-600 transition-colors"
              title=""
            >
              <Edit className="w-4 h-4" />
            </button>
            <button
              onClick={() => handleDeleteClick(user)}
              className="p-2 rounded-full bg-red-50 hover:bg-red-100 text-red-600 transition-colors"
              title=""
            >
              <Trash2 className="w-4 h-4" />
            </button>
          </div>
        </div>
      </div>

      {/* Desktop Layout */}
      <div className="hidden sm:grid grid-cols-6 gap-4 items-center">
        {/* User ID */}
        <div>
          <div className="text-xs font-medium text-gray-500 mb-1">ID</div>
          <div className="font-mono text-sm bg-gray-100 text-gray-700 px-2 py-1 rounded inline-block">
            {user.user_id || 'N/A'}
          </div>
        </div>
        
        {/* Name */}
        <div>
          <div className="text-xs font-medium text-gray-500 mb-1"></div>
          <div className="font-semibold text-gray-900">{user.name}</div>
        </div>
        
        {/* Email */}
        <div>
          <div className="text-xs font-medium text-gray-500 mb-1"></div>
          <div className="text-sm text-gray-700 break-all">{user.email}</div>
        </div>
        
        {/* Role */}
        <div>
          <div className="text-xs font-medium text-gray-500 mb-1"></div>
          <span className={`px-2 py-1 rounded-full text-xs font-medium ${getRoleBadgeStyle(user.role)}`}>
            {getRoleLabel(user.role)}
          </span>
        </div>
        
        {/* Team */}
        <div>
          <div className="text-xs font-medium text-gray-500 mb-1"></div>
          <div className="text-sm text-gray-700">{getTeamName(user.team_id)}</div>
        </div>
        
        {/* Actions */}
        <div className="flex justify-end space-x-2">
          <button
            onClick={() => handleEditClick(user)}
            className="p-2 rounded-full bg-blue-50 hover:bg-blue-100 text-blue-600 transition-colors"
            title=""
          >
            <Edit className="w-4 h-4" />
          </button>
          <button
            onClick={() => handleDeleteClick(user)}
            className="p-2 rounded-full bg-red-50 hover:bg-red-100 text-red-600 transition-colors"
            title=""
          >
            <Trash2 className="w-4 h-4" />
          </button>
        </div>
      </div>
    </div>
  );

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-3">
          <Users className="w-6 h-6 text-purple-600" />
          <h2 className="text-xl font-semibold text-gray-900"></h2>
        </div>
        <div className="text-sm text-gray-600">
          {users.length}
        </div>
      </div>

      {/* Filters */}
      <div className="bg-white border border-gray-200 rounded-lg p-4">
        <div className="flex flex-col sm:flex-row gap-4">
          {/* Search */}
          <div className="flex-1">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
              <input
                type="text"
                placeholder="ID..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
          </div>

          {/* Role Filter */}
          <div className="sm:w-48">
            <div className="relative">
              <Filter className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
              <select
                value={roleFilter}
                onChange={(e) => setRoleFilter(e.target.value)}
                className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent appearance-none"
              >
                <option value="all"></option>
                <option value="global_admin"></option>
                <option value="staff"></option>
                <option value="athlete"></option>
              </select>
            </div>
          </div>
        </div>

        {/* Filter Summary */}
        {(searchTerm || roleFilter !== 'all') && (
          <div className="mt-3 pt-3 border-t border-gray-200">
            <div className="flex items-center justify-between text-sm">
              <div className="text-gray-600">
                : {filteredUsers.length}
                {searchTerm && (
                  <span className="ml-2">
                    {searchTerm}
                  </span>
                )}
                {roleFilter !== 'all' && (
                  <span className="ml-2">
                    : {getRoleLabel(roleFilter)}
                  </span>
                )}
              </div>
              <button
                onClick={() => {
                  setSearchTerm('');
                  setRoleFilter('all');
                }}
                className="text-blue-600 hover:text-blue-700 underline"
              >
                
              </button>
            </div>
          </div>
        )}
      </div>

      {/* Error Display */}
      {error && (
        <div className="flex items-center space-x-3 p-4 bg-red-50 border border-red-200 rounded-lg text-red-600">
          <AlertCircle className="w-5 h-5 flex-shrink-0" />
          <span className="text-sm">{error}</span>
        </div>
      )}

      {/* User Statistics */}
      <div className="grid grid-cols-1 sm:grid-cols-4 gap-4">
        <div className="bg-purple-50 border border-purple-200 rounded-lg p-4 text-center">
          <div className="text-2xl font-bold text-purple-600">
            {users.filter(u => u.role === 'global_admin').length}
          </div>
          <div className="text-sm text-purple-700"></div>
        </div>
        <div className="bg-green-50 border border-green-200 rounded-lg p-4 text-center">
          <div className="text-2xl font-bold text-green-600">
            {users.filter(u => u.role === 'staff').length}
          </div>
          <div className="text-sm text-green-700"></div>
        </div>
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-4 text-center">
          <div className="text-2xl font-bold text-blue-600">
            {users.filter(u => u.role === 'athlete').length}
          </div>
          <div className="text-sm text-blue-700"></div>
        </div>
        <div className="bg-gray-50 border border-gray-200 rounded-lg p-4 text-center">
          <div className="text-2xl font-bold text-gray-600">
            {users.length}
          </div>
          <div className="text-sm text-gray-700"></div>
        </div>
      </div>

      {/* User Table */}
      <div className="bg-white border border-gray-200 rounded-lg">
        <div className="p-4 border-b border-gray-200">
          <h3 className="font-medium text-gray-900"></h3>
        </div>
        
        <div className="p-4">
          <PaginatedTable
            data={filteredUsers}
            itemsPerPage={10}
            renderItem={renderUserRow}
            loading={loading}
            emptyMessage={
              searchTerm || roleFilter !== 'all' 
                ? ""
                : ""
            }
          />
        </div>
      </div>

      {/* Modals */}
      {selectedUser && showEditModal && (
        <UserEditModal
          user={selectedUser}
          teams={teams}
          onClose={() => {
            setShowEditModal(false);
            setSelectedUser(null);
          }}
          onUserUpdated={handleUserUpdated}
        />
      )}

      {selectedUser && showDeleteModal && (
        <UserDeleteModal
          user={selectedUser}
          onClose={() => {
            setShowDeleteModal(false);
            setSelectedUser(null);
          }}
          onUserDeleted={handleUserDeleted}
        />
      )}
    </div>
  );
}