// src/components/ProfileEditForm.tsx
import React, { useState } from 'react';
import { User as UserIcon, Calendar, Ruler, Users, Save, AlertCircle, Phone } from 'lucide-react';
import { supabase } from '../lib/supabase';
import { Database } from '../lib/database.types';
import { getYearsAgoString } from '../lib/date';

type UserProfile = Database['public']['Tables']['users']['Row'];
type UserUpdate = Database['public']['Tables']['users']['Update'];

interface ProfileEditFormProps {
  user: UserProfile;
  onUpdate: () => void;
  onClose: () => void;
}

/**
 * 
 * - 
 * - //
 * - +81 / 81  0 : +8190... / 8190...  090...
 */
function normalizePhoneNumber(input: string) {
  if (!input) return '';
  const half = input.replace(/[-]/g, (s) => String.fromCharCode(s.charCodeAt(0) - 0xfee0));
  let digits = half.replace(/\D/g, '');

  // 81  0 
  if (digits.startsWith('81')) {
    digits = '0' + digits.slice(2);
  }
  return digits;
}

/**
 * 070 / 080 / 090
 * - OK
 * - 0[7-9]0 + 8 = 11
 */
function isLikelyJpMobile(phoneDigits: string) {
  if (!phoneDigits) return true;
  return /^0[7-9]0\d{8}$/.test(phoneDigits);
}

export function ProfileEditForm({ user, onUpdate, onClose }: ProfileEditFormProps) {
  const [name, setName] = useState(user.name ?? '');
  const [gender, setGender] = useState<string>(user.gender || '');
  const [heightCm, setHeightCm] = useState<string>(user.height_cm?.toString() || '');
  const [dateOfBirth, setDateOfBirth] = useState<string>(user.date_of_birth || '');
  const [phoneNumber, setPhoneNumber] = useState<string>(user.phone_number || '');

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string>('');
  const [success, setSuccess] = useState(false);

  const calculateAge = () => {
    if (!dateOfBirth) return null;
    const birth = new Date(dateOfBirth);
    const today = new Date();
    let age = today.getFullYear() - birth.getFullYear();
    const monthDiff = today.getMonth() - birth.getMonth();
    if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birth.getDate())) age--;
    return age;
  };

  const age = calculateAge();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setSuccess(false);
    setLoading(true);

    try {
      const trimmedName = name.trim();
      if (!trimmedName) throw new Error('');

      const parsedHeight = heightCm ? Number(heightCm) : null;
      if (parsedHeight !== null && (Number.isNaN(parsedHeight) || parsedHeight < 100 || parsedHeight > 250)) {
        throw new Error('100250cm');
      }

      if (dateOfBirth) {
        const birthDate = new Date(dateOfBirth);
        const today = new Date();
        const ageGuess = today.getFullYear() - birthDate.getFullYear();
        if (Number.isNaN(birthDate.getTime()) || ageGuess < 10 || ageGuess > 150) {
          throw new Error('10');
        }
      }

      const normalizedPhone = normalizePhoneNumber(phoneNumber);

      // 
      if (!isLikelyJpMobile(normalizedPhone)) {
        throw new Error('070 / 080 / 090OK');
      }

      const updateData: UserUpdate = {
        name: trimmedName,
        gender: gender || null,
        height_cm: parsedHeight,
        date_of_birth: dateOfBirth || null,
        phone_number: normalizedPhone || null, // 
      };

      const { error: updateError } = await supabase.from('users').update(updateData).eq('id', user.id);
      if (updateError) throw updateError;

      setSuccess(true);
      setTimeout(() => {
        onUpdate();
        onClose();
      }, 1200);
    } catch (err) {
      console.error('Error updating profile:', err);
      setError(err instanceof Error ? err.message : '');
    } finally {
      setLoading(false);
    }
  };

  const normalizedPreview = normalizePhoneNumber(phoneNumber);

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
      <div className="bg-white dark:bg-gray-800 rounded-lg shadow-xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
        <div className="p-6">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-2xl font-bold text-gray-900 dark:text-white flex items-center">
              <UserIcon className="w-6 h-6 mr-2" />
              
            </h2>
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-gray-600 dark:hover:text-gray-300"
              type="button"
            >
              <span className="text-2xl">&times;</span>
            </button>
          </div>

          <form onSubmit={handleSubmit} className="space-y-6">
            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2"></label>
              <input
                type="text"
                value={name}
                onChange={(e) => setName(e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent bg-white dark:bg-gray-700 dark:text-white"
                required
              />
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2 flex items-center">
                <Phone className="w-4 h-4 mr-2" />
                
              </label>
              <input
                type="tel"
                inputMode="tel"
                value={phoneNumber}
                onChange={(e) => setPhoneNumber(e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent bg-white dark:bg-gray-700 dark:text-white"
                placeholder="090-1234-5678+81 OK"
              />
              <div className="text-xs text-gray-500 dark:text-gray-400 mt-1 space-y-1">
                <p>CSV InBody </p>
                {!!phoneNumber && (
                  <p>
                    : <span className="font-mono">{normalizedPreview}</span>
                  </p>
                )}
              </div>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2 flex items-center">
                <Users className="w-4 h-4 mr-2" />
                
              </label>
              <select
                value={gender}
                onChange={(e) => setGender(e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent bg-white dark:bg-gray-700 dark:text-white"
              >
                <option value=""></option>
                <option value="male"></option>
                <option value="female"></option>
                <option value="other"></option>
                <option value="prefer_not_to_say"></option>
              </select>
              <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                
              </p>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2 flex items-center">
                <Ruler className="w-4 h-4 mr-2" />
                cm
              </label>
              <input
                type="number"
                step="0.1"
                min="100"
                max="250"
                value={heightCm}
                onChange={(e) => setHeightCm(e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent bg-white dark:bg-gray-700 dark:text-white"
                placeholder="175.5"
              />
              <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                BMI100250cm
              </p>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2 flex items-center">
                <Calendar className="w-4 h-4 mr-2" />
                
              </label>
              <input
                type="date"
                value={dateOfBirth}
                onChange={(e) => setDateOfBirth(e.target.value)}
                max={getYearsAgoString(10)}
                className="w-full px-4 py-3 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent bg-white dark:bg-gray-700 dark:text-white"
              />
              {age !== null && (
                <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">: {age}</p>
              )}
              <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                
              </p>
            </div>

            {/*  */}
            {error && (
              <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg p-4">
                <div className="flex items-center">
                  <AlertCircle className="w-5 h-5 text-red-600 dark:text-red-400 mr-2" />
                  <span className="text-sm text-red-700 dark:text-red-300">{error}</span>
                </div>
              </div>
            )}

            {/*  */}
            {success && (
              <div className="bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-800 rounded-lg p-4">
                <div className="flex items-center">
                  <Save className="w-5 h-5 text-green-600 dark:text-green-400 mr-2" />
                  <span className="text-sm text-green-700 dark:text-green-300"></span>
                </div>
              </div>
            )}

            {/*  */}
            <div className="flex space-x-3 pt-4">
              <button
                type="submit"
                disabled={loading || success}
                className="flex-1 bg-blue-600 text-white py-3 px-4 rounded-lg hover:bg-blue-700 focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 transition-colors disabled:opacity-50 disabled:cursor-not-allowed font-medium flex items-center justify-center"
              >
                {loading ? (
                  <>
                    <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white mr-2"></div>
                    ...
                  </>
                ) : (
                  <>
                    <Save className="w-5 h-5 mr-2" />
                    
                  </>
                )}
              </button>

              <button
                type="button"
                onClick={onClose}
                disabled={loading}
                className="flex-1 bg-gray-200 dark:bg-gray-700 text-gray-700 dark:text-gray-300 py-3 px-4 rounded-lg hover:bg-gray-300 dark:hover:bg-gray-600 transition-colors disabled:opacity-50 disabled:cursor-not-allowed font-medium"
              >
                
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}