import React, { useState } from 'react';
import { User, supabase } from '../lib/supabase';
import { X, Trash2, AlertTriangle, CheckCircle } from 'lucide-react';

interface UserDeleteModalProps {
  user: User;
  onClose: () => void;
  onUserDeleted: () => void;
}

export function UserDeleteModal({ user, onClose, onUserDeleted }: UserDeleteModalProps) {
  const [loading, setLoading] = useState(false);
  const [confirmText, setConfirmText] = useState('');
  const [message, setMessage] = useState<{ type: 'success' | 'error'; text: string } | null>(null);

  const handleDelete = async () => {
    if (confirmText !== user.name) {
      setMessage({
        type: 'error',
        text: ''
      });
      return;
    }

    setLoading(true);
    setMessage(null);

    try {
      const { data: { session } } = await supabase.auth.getSession();
      
      if (!session) {
        throw new Error('');
      }

      // Call the delete user function
      const supabaseUrl = import.meta.env.VITE_SUPABASE_URL;

      const response = await fetch(`${supabaseUrl}/functions/v1/delete-user`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${session.access_token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          userId: user.id
        }),
      });

      const result = await response.json();

      if (!response.ok) {
        throw new Error(result.error || '');
      }

      setMessage({
        type: 'success',
        text: ''
      });

      setTimeout(() => {
        onUserDeleted();
      }, 1000);

    } catch (error: any) {
      console.error('Error deleting user:', error);
      setMessage({
        type: 'error',
        text: `: ${error.message}`
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4">
      <div className="bg-white rounded-2xl shadow-xl max-w-md w-full">
        {/* Header */}
        <div className="bg-red-600 text-white p-6 rounded-t-2xl">
          <div className="flex items-center justify-between">
            <div className="flex items-center">
              <AlertTriangle className="w-6 h-6 mr-3" />
              <h2 className="text-xl font-bold"></h2>
            </div>
            <button
              onClick={onClose}
              className="bg-red-500 hover:bg-red-400 rounded-full p-2 transition-colors"
            >
              <X className="w-5 h-5" />
            </button>
          </div>
        </div>

        {/* Content */}
        <div className="p-6">
          <div className="mb-6">
            <div className="bg-red-50 border border-red-200 rounded-lg p-4 mb-4">
              <div className="flex items-start">
                <AlertTriangle className="w-5 h-5 text-red-600 mr-2 mt-0.5" />
                <div>
                  <h3 className="font-medium text-red-900 mb-2">: </h3>
                  <div className="text-sm text-red-700 space-y-1">
                    <p> </p>
                    <p> </p>
                    <p> </p>
                  </div>
                </div>
              </div>
            </div>

            <div className="bg-gray-50 rounded-lg p-4">
              <h4 className="font-medium text-gray-900 mb-2"></h4>
              <div className="text-sm text-gray-700">
                <p><strong>:</strong> {user.name}</p>
                <p><strong>:</strong> {user.email}</p>
                <p><strong>:</strong> {
                  user.role === 'athlete' ? '' : 
                  user.role === 'staff' ? '' : ''
                }</p>
              </div>
            </div>
          </div>

          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              <strong>{user.name}</strong>
            </label>
            <input
              type="text"
              value={confirmText}
              onChange={(e) => setConfirmText(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent"
              placeholder={user.name}
            />
          </div>

          {message && (
            <div className={`flex items-center space-x-3 p-3 rounded-lg mb-4 ${
              message.type === 'success' 
                ? 'bg-green-50 border border-green-200 text-green-600'
                : 'bg-red-50 border border-red-200 text-red-600'
            }`}>
              {message.type === 'success' ? (
                <CheckCircle className="w-5 h-5 flex-shrink-0" />
              ) : (
                <AlertTriangle className="w-5 h-5 flex-shrink-0" />
              )}
              <span className="text-sm">{message.text}</span>
            </div>
          )}

          <div className="flex space-x-3">
            <button
              type="button"
              onClick={onClose}
              className="flex-1 bg-gray-100 hover:bg-gray-200 text-gray-700 py-2 px-4 rounded-lg transition-colors"
            >
              
            </button>
            <button
              onClick={handleDelete}
              disabled={loading || confirmText !== user.name}
              className="flex-1 bg-red-600 hover:bg-red-700 text-white py-2 px-4 rounded-lg transition-colors disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center"
            >
              {loading ? (
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
              ) : (
                <Trash2 className="w-4 h-4 mr-2" />
              )}
              {loading ? '...' : ''}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}