// src/components/TeamACWRChart.tsx
import React, { useState, useMemo } from 'react';
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
  ReferenceLine,
  Legend,
} from 'recharts';
import { getRiskColor } from '../lib/acwr';
import { Filter } from 'lucide-react';

interface TeamACWRData {
  date: string;
  averageACWR: number;
  athleteCount: number;
  riskLevel: string;

  //  DBhook
  // 
  averageRPE?: number | null;
  avg_rpe?: number | null;
  rpe_avg?: number | null;

  averageLoad?: number | null;
  avg_load?: number | null;
  load_avg?: number | null;

  // OK
  rosterCount?: number | null;
}

interface TeamACWRChartProps {
  data: TeamACWRData[];
  teamName: string;

  //  StaffView 
  showAvgRPE?: boolean;
  showAvgLoad?: boolean;
}

const pickAvgRPE = (d: TeamACWRData) => d.averageRPE ?? d.avg_rpe ?? d.rpe_avg ?? null;
const pickAvgLoad = (d: TeamACWRData) => d.averageLoad ?? d.avg_load ?? d.load_avg ?? null;

/**  ON/OFF */
function MetricTogglePills({
  showRPE,
  showLoad,
  onToggle,
}: {
  showRPE: boolean;
  showLoad: boolean;
  onToggle: (key: 'rpe' | 'load') => void;
}) {
  return (
    <div className="flex items-center gap-2">
      <span className="hidden sm:inline text-xs text-gray-500 mr-1"></span>

      {/* RPE */}
      <button
        type="button"
        onClick={() => onToggle('rpe')}
        className={[
          'inline-flex items-center gap-2 rounded-xl border px-3 py-2 text-sm font-semibold',
          'shadow-sm transition',
          'focus:outline-none focus-visible:ring-2 focus-visible:ring-blue-500',
          showRPE
            ? 'bg-white border-blue-200 text-blue-700'
            : 'bg-gray-50 border-gray-200 text-gray-500 hover:bg-white',
        ].join(' ')}
        aria-pressed={showRPE}
      >
        <span
          className={[
            'inline-flex h-5 w-9 items-center rounded-full p-0.5 transition',
            showRPE ? 'bg-blue-600' : 'bg-gray-300',
          ].join(' ')}
        >
          <span
            className={[
              'h-4 w-4 rounded-full bg-white shadow transition',
              showRPE ? 'translate-x-4' : 'translate-x-0',
            ].join(' ')}
          />
        </span>
        RPE
      </button>

      {/* Load */}
      <button
        type="button"
        onClick={() => onToggle('load')}
        className={[
          'inline-flex items-center gap-2 rounded-xl border px-3 py-2 text-sm font-semibold',
          'shadow-sm transition',
          'focus:outline-none focus-visible:ring-2 focus-visible:ring-blue-500',
          showLoad
            ? 'bg-white border-emerald-200 text-emerald-700'
            : 'bg-gray-50 border-gray-200 text-gray-500 hover:bg-white',
        ].join(' ')}
        aria-pressed={showLoad}
      >
        <span
          className={[
            'inline-flex h-5 w-9 items-center rounded-full p-0.5 transition',
            showLoad ? 'bg-emerald-600' : 'bg-gray-300',
          ].join(' ')}
        >
          <span
            className={[
              'h-4 w-4 rounded-full bg-white shadow transition',
              showLoad ? 'translate-x-4' : 'translate-x-0',
            ].join(' ')}
          />
        </span>
        Load
      </button>
    </div>
  );
}

export function TeamACWRChart({
  data,
  teamName,
  showAvgRPE = true,
  showAvgLoad = false,
}: TeamACWRChartProps) {
  const [selectedPeriod, setSelectedPeriod] = useState<'week' | 'month' | 'quarter' | 'all'>('month');

  //  StaffView + ON/OFF
  const [showRPEUI, setShowRPEUI] = useState<boolean>(showAvgRPE);
  const [showLoadUI, setShowLoadUI] = useState<boolean>(showAvgLoad);

  //  useEffect 
  // React.useEffect(() => setShowRPEUI(showAvgRPE), [showAvgRPE]);
  // React.useEffect(() => setShowLoadUI(showAvgLoad), [showAvgLoad]);

  //  period filter
  const filteredData = useMemo(() => {
    if (!data || data.length === 0) return [];

    const today = new Date();
    let startDate: Date;

    switch (selectedPeriod) {
      case 'week':
        startDate = new Date(today);
        startDate.setDate(today.getDate() - 7);
        break;
      case 'month':
        startDate = new Date(today);
        startDate.setDate(today.getDate() - 30);
        break;
      case 'quarter':
        startDate = new Date(today);
        startDate.setDate(today.getDate() - 90);
        break;
      case 'all':
      default:
        return data;
    }

    return data.filter((item) => {
      const itemDate = new Date(item.date);
      return itemDate >= startDate && itemDate <= today;
    });
  }, [data, selectedPeriod]);

  //  RPE/Loadrecharts dataKey
  const chartData = useMemo(() => {
    return (filteredData || []).map((d) => ({
      ...d,
      avgRPE: pickAvgRPE(d),
      avgLoad: pickAvgLoad(d),
    }));
  }, [filteredData]);

  //  data
  const safeData = useMemo(() => {
    return (chartData || [])
      .filter((d: any) => {
        if (!d || typeof d.date !== 'string') return false;

        // ACWR
        if (typeof d.averageACWR !== 'number' || !Number.isFinite(d.averageACWR)) return false;

        // auxRPE/LoadOKnullmap
        return true;
      })
      .map((d: any) => ({
        ...d,
        averageACWR: Math.max(0, d.averageACWR),
        avgRPE: typeof d.avgRPE === 'number' && Number.isFinite(d.avgRPE) ? d.avgRPE : null,
        avgLoad: typeof d.avgLoad === 'number' && Number.isFinite(d.avgLoad) ? d.avgLoad : null,
      }));
  }, [chartData]);

  const hasRPE = useMemo(() => safeData.some((d: any) => typeof d.avgRPE === 'number'), [safeData]);
  const hasLoad = useMemo(() => safeData.some((d: any) => typeof d.avgLoad === 'number'), [safeData]);

  const showRPELine = !!showRPEUI && hasRPE;
  const showLoadLine = !!showLoadUI && hasLoad;
  const showAuxAxis = showRPELine || showLoadLine;

  if (!data || data.length === 0) {
    return (
      <div className="flex items-center justify-center h-64 sm:h-96 text-gray-500">
        <div className="text-center px-4">
          <p className="text-base sm:text-lg mb-2"></p>
          <p className="text-sm">ACWR</p>
        </div>
      </div>
    );
  }

  //  filtered
  if (safeData.length === 0) {
    return (
      <div className="flex items-center justify-center h-64 sm:h-96 text-gray-500">
        <div className="text-center px-4">
          <p className="text-base sm:text-lg mb-2">ACWR</p>
          <p className="text-sm"></p>
        </div>
      </div>
    );
  }

  const formatDate = (dateStr: string) => {
    const date = new Date(dateStr);
    return `${date.getMonth() + 1}/${date.getDate()}`;
  };

  const formatDateWithDay = (dateStr: string) => {
    const date = new Date(dateStr);
    const dayNames = ['', '', '', '', '', '', ''];
    const dayOfWeek = dayNames[date.getDay()];
    return `${date.getMonth() + 1}/${date.getDate()}(${dayOfWeek})`;
  };

  const CustomTooltip = ({ active, payload, label }: any) => {
    if (active && payload && payload.length) {
      const row = payload?.[0]?.payload;
      const date = new Date(label);
      const isWeekend = date.getDay() === 0 || date.getDay() === 6;

      const acwr = row?.averageACWR;
      const rpe = row?.avgRPE;
      const load = row?.avgLoad;

      return (
        <div className="bg-white p-3 sm:p-4 border border-gray-200 rounded-lg shadow-lg max-w-xs">
          <div className="flex items-center space-x-2 mb-2">
            <p className="font-medium text-gray-900 text-sm sm:text-base">{formatDateWithDay(label)}</p>
            {isWeekend && (
              <span className="bg-purple-100 text-purple-700 px-2 py-1 rounded-full text-xs font-medium">
                
              </span>
            )}
          </div>

          <div className="space-y-1 text-xs sm:text-sm">
            <p className="flex justify-between">
              <span>ACWR:</span>
              <span className="font-semibold" style={{ color: getRiskColor(row?.riskLevel) }}>
                {typeof acwr === 'number' ? acwr.toFixed(2) : '-'}
              </span>
            </p>

            {showRPELine && (
              <p className="flex justify-between">
                <span>RPE:</span>
                <span className="font-semibold">{typeof rpe === 'number' ? rpe.toFixed(2) : '-'}</span>
              </p>
            )}

            {showLoadLine && (
              <p className="flex justify-between">
                <span>Load:</span>
                <span className="font-semibold">{typeof load === 'number' ? Math.round(load) : '-'}</span>
              </p>
            )}

            <p className="flex justify-between">
              <span>:</span>
              <span className="font-semibold">
                {row?.athleteCount} / {row?.rosterCount ?? '-'}
              </span>
            </p>

            <p className="flex justify-between">
              <span>:</span>
              <span className="font-semibold" style={{ color: getRiskColor(row?.riskLevel) }}>
                {getRiskLabel(row?.riskLevel)}
              </span>
            </p>
          </div>
        </div>
      );
    }
    return null;
  };

  const CustomDot = (props: any) => {
    const { cx, cy, payload } = props;
    if (cx && cy && payload) {
      const date = new Date(payload.date);
      const isWeekend = date.getDay() === 0 || date.getDay() === 6;

      return (
        <circle
          cx={cx}
          cy={cy}
          r={isWeekend ? 5 : 4}
          fill={getRiskColor(payload.riskLevel)}
          strokeWidth={2}
          stroke={isWeekend ? '#8B5CF6' : 'white'}
          opacity={isWeekend ? 0.9 : 1}
        />
      );
    }
    return null;
  };

  return (
    <div className="w-full">
      {/*  */}
      <div className="mb-4 space-y-4">
        {/*   /  */}
        <div className="flex flex-col gap-3 sm:flex-row sm:items-start sm:justify-between">
          <div>
            <h3 className="text-base sm:text-lg font-semibold text-gray-900 truncate">
              <span className="hidden sm:inline">{teamName} - </span>ACWR
            </h3>
            <p className="text-xs sm:text-sm text-gray-600">
              ACWR{showAuxAxis ? ' / RPELoad' : ''}
            </p>
          </div>

          <MetricTogglePills
            showRPE={showRPEUI}
            showLoad={showLoadUI}
            onToggle={(key) => {
              if (key === 'rpe') setShowRPEUI((v) => !v);
              if (key === 'load') setShowLoadUI((v) => !v);
            }}
          />
        </div>

        {/*  */}
        <div className="flex items-center space-x-2">
          <Filter className="w-4 h-4 text-gray-500" />
          <div className="flex space-x-1">
            {[
              { value: 'week', label: '1' },
              { value: 'month', label: '1' },
              { value: 'quarter', label: '3' },
              { value: 'all', label: '' },
            ].map((period) => (
              <button
                key={period.value}
                onClick={() => setSelectedPeriod(period.value as any)}
                className={`px-3 py-1 rounded-lg text-xs font-medium transition-colors ${
                  selectedPeriod === period.value
                    ? 'bg-purple-600 text-white'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                {period.label}
              </button>
            ))}
          </div>
        </div>

        {/*  */}
        <div className="bg-purple-50 rounded-lg p-3">
          <div className="flex items-center justify-between text-sm">
            <div className="flex items-center space-x-4">
              <div>
                <span className="text-purple-600 font-medium">:</span>
                <span className="text-purple-800 ml-1">
                  {selectedPeriod === 'week'
                    ? '7'
                    : selectedPeriod === 'month'
                    ? '30'
                    : selectedPeriod === 'quarter'
                    ? '90'
                    : ''}
                </span>
              </div>
              <div>
                <span className="text-purple-600 font-medium">:</span>
                <span className="text-purple-800 ml-1">{safeData.length}</span>
              </div>
            </div>
            <div className="flex items-center space-x-1">
              <div className="w-3 h-3 bg-purple-500 rounded-full border-2 border-purple-600"></div>
              <span className="text-xs text-purple-600"></span>
            </div>
          </div>
        </div>

        {/*   */}
        {(showRPEUI && !hasRPE) || (showLoadUI && !hasLoad) ? (
          <div className="text-xs bg-amber-50 border border-amber-200 text-amber-800 rounded-lg px-3 py-2">
            ONRPE/Load
          </div>
        ) : null}
      </div>

      <div className="h-64 sm:h-96">
        <ResponsiveContainer width="100%" height="100%">
          {/*  safeData  */}
          <LineChart data={safeData} margin={{ top: 20, right: 36, left: 20, bottom: 20 }}>
            <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />

            <XAxis
              dataKey="date"
              tickFormatter={formatDate}
              stroke="#6b7280"
              fontSize={10}
              interval="preserveStartEnd"
            />

            {/*  ACWR */}
            <YAxis
              yAxisId="acwr"
              domain={[0, (dataMax: number) => (Number.isFinite(dataMax) ? dataMax + 0.5 : 2)]}
              stroke="#6b7280"
              fontSize={10}
              width={40}
            />

            {/*  RPE/Load */}
            <YAxis
              yAxisId="aux"
              orientation="right"
              domain={[0, (dataMax: number) => (Number.isFinite(dataMax) ? dataMax : 10)]}
              stroke="#6b7280"
              fontSize={10}
              width={40}
              hide={!showAuxAxis}
            />

            <Tooltip content={<CustomTooltip />} />
            <Legend />

            {/* Reference lines for risk zones */}
            <ReferenceLine yAxisId="acwr" y={1.5} stroke="#EF4444" strokeDasharray="5 5" />
            <ReferenceLine yAxisId="acwr" y={1.3} stroke="#F59E0B" strokeDasharray="5 5" />
            <ReferenceLine yAxisId="acwr" y={0.8} stroke="#10B981" strokeDasharray="5 5" />

            {/* ACWR */}
            <Line
              type="monotone"
              dataKey="averageACWR"
              name="ACWR"
              yAxisId="acwr"
              stroke="#8B5CF6"
              strokeWidth={3}
              dot={<CustomDot />}
              activeDot={{ r: 6, stroke: '#8B5CF6', strokeWidth: 2, fill: 'white' }}
              connectNulls
            />

            {/* RPEavgRPE */}
            {showRPELine && (
              <Line
                type="monotone"
                dataKey="avgRPE"
                name="RPE"
                yAxisId="aux"
                stroke="#0EA5E9"
                strokeWidth={2}
                dot={false}
                connectNulls
              />
            )}

            {/* LoadavgLoad */}
            {showLoadLine && (
              <Line
                type="monotone"
                dataKey="avgLoad"
                name="Load"
                yAxisId="aux"
                stroke="#22C55E"
                strokeWidth={2}
                dot={false}
                connectNulls
              />
            )}
          </LineChart>
        </ResponsiveContainer>
      </div>

      {/* Legend - Centered at bottom */}
      <div className="mt-4 flex justify-center">
        <div className="grid grid-cols-2 sm:flex sm:flex-wrap gap-2 sm:gap-4 text-xs">
          <div className="flex items-center">
            <div className="w-3 h-3 bg-red-500 rounded-full mr-2"></div>
            <span> (&gt;1.5)</span>
          </div>
          <div className="flex items-center">
            <div className="w-3 h-3 bg-yellow-500 rounded-full mr-2"></div>
            <span> (1.3-1.5)</span>
          </div>
          <div className="flex items-center">
            <div className="w-3 h-3 bg-green-500 rounded-full mr-2"></div>
            <span> (0.8-1.3)</span>
          </div>
          <div className="flex items-center">
            <div className="w-3 h-3 bg-blue-500 rounded-full mr-2"></div>
            <span> (&lt;0.8)</span>
          </div>
        </div>
      </div>
    </div>
  );
}

function getRiskLabel(riskLevel: string): string {
  switch (riskLevel) {
    case 'high':
      return '';
    case 'caution':
      return '';
    case 'good':
      return '';
    case 'low':
      return '';
    default:
      return '';
  }
}